import React, { useState, useRef } from "react";
import { useHistory } from "react-router-dom";
import SelectorsContainer from "../components/SelectorsContainer";
import ArrowDown from "../assets/svgs/arrowDown.svg";
import RedDot from "../assets/svgs/redDot.svg";
import BannerImg from "../assets/svgs/bannerImg.svg";
import useOnClickOutside from "../hooks/useOnClickOutside";

const Home = () => {
  const history = useHistory();
  const ref = useRef();

  useOnClickOutside(ref, () => onSetExpand(false));
  const [expand, setExpand] = useState(false);
  const [appear, setAppear] = useState(false);

  const onSetExpand = value => {
    setExpand(value);
    setAppear(true);
  };

  return (
    <div className="home">
      <div onClick={() => onSetExpand(true)}>
        <div className="content">
          {/* <img
              src={CardBadge}
              alt=""
              onClick={() => onSetExpand(true)}
              className="hand-cursor select-badge"
            /> */}
          <img src={BannerImg} className="home-badge" alt="" />
          {!expand && (
            <div className="badge-content">
              <div className="badge-div"></div>
              <div className="badge-desc ml-4">
                <div>Encuentra la casa</div>
                <div className="d-flex align-items-baseline dotContent">
                  <div>de tus sueños</div>
                  <img
                    src={RedDot}
                    alt=""
                    className={appear === false ? "fadeInUpDot ml-5" : "ml-5"}
                  />
                </div>
              </div>
            </div>
          )}
        </div>
        <div ref={ref}>
          <SelectorsContainer expand={expand} />
        </div>
      </div>
      <img
        src={ArrowDown}
        alt=""
        className="arrow-down"
        onClick={() => history.push("resident-zone")}
      />
    </div>
  );
};

export default Home;
