import React from "react";
import { useSelector, useDispatch } from "react-redux";

import CloseBtn from "../assets/closeBtn.png";
import PHONE_BLACK from "../assets/phone.png";
import FB_BLACK from "../assets/Facebook-black.png";
import INSTA_BLACK from "../assets/Instagram-black.png";

const SideBar = () => {
  const dispatch = useDispatch();
  const show = useSelector(state => state.sidebarShow);

  if (show) {
    return (
      <div className="sideBar">
        <div className="text-center">
          <img
            className="closeBtn"
            onClick={() => dispatch({ type: "set", sidebarShow: false })}
            alt=""
            src={CloseBtn}
          />
        </div>
        <ul className="titles">
          <li>
            <a href="/team">QUIENES SOMOS</a>
          </li>
          <li>
            <a href="/development">DESARROLOS</a>
          </li>
          <li>
            <a href="/contacts">PROPIETARIOS</a>
          </li>
          <li>
            <a href="/jobs">EMPLEO</a>
          </li>
        </ul>
        <ul className="contacts">
          <li className="row align-items-center">
            <div className="col-2 col-md-1 text-center">
              <img src={FB_BLACK} alt="" className="icon" />
            </div>
            <a
              className="col-10 col-md-11"
              href="https://www.facebook.com/myminmobiliarios"
            >
              fb.me/myminmobiliarios
            </a>
          </li>
          <li className="row align-items-center">
            <div className="col-2 col-md-1 text-center">
              <img src={INSTA_BLACK} alt="" className="instagram" />
            </div>
            <a
              className="col-10 col-md-11"
              href="https://www.instagram.com/myminmobiliarios/"
            >
              instagram.com/myminmobiliarios
            </a>
          </li>
          <li className="row align-items-center">
            <div className="col-2 col-md-1 text-center">
              <img src={PHONE_BLACK} alt="" className="phone" />
            </div>
            <a href="tel:52573737" className="col-10 col-md-11">
              52573737
            </a>
          </li>
        </ul>
      </div>
    );
  } else {
    return <></>;
  }
};

export default React.memo(SideBar);
