import React from "react";
import { useHistory } from "react-router-dom";
import { isMobile } from "react-device-detect";
import Category1 from "../assets/svgs/category1.svg";
import Category2 from "../assets/svgs/category2.svg";
import Category3 from "../assets/svgs/category3.svg";
import Category4 from "../assets/svgs/category4.svg";

//mobile
import CatMob1 from "../assets/svgs/catMob1.svg";
import CatMob2 from "../assets/svgs/catMob2.svg";
import CatMob3 from "../assets/svgs/catMob3.svg";
import CatMob4 from "../assets/svgs/catMob4.svg";

const categories = [
  {
    id: 1,
    name: "Lomas",
    image: Category1,
    mobImg: CatMob1
  },
  {
    id: 2,
    name: "Bosques",
    image: Category2,
    mobImg: CatMob2
  },
  {
    id: 3,
    name: "Polanco",
    image: Category3,
    mobImg: CatMob3
  },
  {
    id: 4,
    name: "Interlomas",
    image: Category4,
    mobImg: CatMob4
  }
];

const ResidentZone = () => {
  const history = useHistory();

  const handleClick = item => {
    history.push(`/resident/${item.name}`, item);
  };
  return (
    <div className="container row mx-auto mt-5 fadeInUp residentZone">
      {categories.map(item => (
        <div
          className="col-12 col-sm-6 col-md-3 px-3 hand-cursor"
          key={item.id}
        >
          <div className="categoryContainer" onClick={() => handleClick(item)}>
            <img
              src={isMobile ? item.mobImg : item.image}
              className="w-100 mt-3"
              alt=""
            />
            {/* <img src={CarydBadge} /> */}
          </div>
          {/* <div className="d-flex">
            <h2>{item.name}</h2>
            <span>.</span>
          </div> */}
        </div>
      ))}
    </div>
  );
};

export default ResidentZone;
