import React from "react";
import Currency from "../assets/svgs/dollar.svg";
import Square from "../assets/svgs/square.svg";
import Car from "../assets/svgs/car.svg";
import Bed from "../assets/svgs/beds.svg";
import CardBadge from "../assets/svgs/cardBadge.svg";

const CardItem = props => {
  const item = props.item;
  const location = props.item.location[0];
  return (
    <div className="card-container">
      <div className="badge-content">
        <div className="imgContent">
          <img src={item.images[0].image[0]} className="w-100" alt="" />
        </div>
        <img src={CardBadge} className="badgeContent" alt="" />
      </div>
      <div>
        <h6>{item.title[0]}</h6>
        <div className="text-right">
          <p>
            {location.address} {location.city} {location.region}
          </p>
          <p>
            <img src={Currency} alt="" className="mr-3" />
            2,120,000 USD
          </p>
          <p>
            <img src={Square} alt="" className="mr-2" />{" "}
            {item.construction_size[0]} ㎡
          </p>
          <p>
            {item.parking_spaces && (
              <span className="mr-3">
                <img src={Car} alt="" className="mr-2" />
                {item.parking_spaces[0]}
              </span>
            )}
            {item.bedrooms && (
              <span>
                <img src={Bed} alt="" className="mr-2" />
                {item.bedrooms[0]}
              </span>
            )}
          </p>
        </div>
      </div>
    </div>
  );
};

export default CardItem;
