import { createStore } from "redux";

const initialState = {
  sidebarShow: false,
  data: [],
  searchedData: [],
  loading: false
};

const changeState = (state = initialState, { type, ...rest }) => {
  switch (type) {
    case "set":
      return { ...state, ...rest };
    case "saveData":
      return { ...state, ...rest };
    case "searchedData":
      return { ...state, ...rest };
    case "gettingData":
      return { ...state, ...rest };
    default:
      return state;
  }
};

const store = createStore(changeState);

export default store;
