import React, { useState, useEffect } from "react";
import Select from "react-select";
import { useDispatch, useSelector } from "react-redux";
import SEARCH_BTN from "../assets/search.png";
import { useHistory } from "react-router-dom";

//property_type
const inmueble = [
  { value: "Casa", label: "Casa" },
  { value: "Departamento", label: "Departamento" },
  { value: "Oficina", label: "Oficina" },
  { value: "Local comercial", label: "Local comercial" },
  { value: "Almacén", label: "Almacén" },
  { value: "Nave Industrial", label: "Nave Industrial" },
  { value: "Terreno", label: "Terreno" },
  { value: "Quinta", label: "Quinta" }
];

const operation = [
  { value: "sale", label: "Venta" },
  { value: "rental", label: "Renta" }
];

const square = [
  { value: "chocolate", label: "Chocolate" },
  { value: "strawberry", label: "Strawberry" },
  { value: "vanilla", label: "Vanilla" }
];

const currency = [
  { value: "MXN", label: "MXN" },
  { value: "USD", label: "USD" }
];
const city = [
  { value: "chocolate", label: "Chocolate" },
  { value: "strawberry", label: "Strawberry" },
  { value: "vanilla", label: "Vanilla" }
];

const city_area = [
  { value: "Lomas", label: "Lomas" },
  { value: "Bosques", label: "Bosques" },
  { value: "Polanco", label: "Polanco" },
  { value: "Interlomas", label: "Interlomas" }
];

const region = [
  { value: "Aguascalientes", label: "Aguascalientes" },
  { value: "Baja California", label: "Baja California" },
  { value: "Baja California Sur", label: "Baja California Sur" },
  { value: "Campeche", label: "Campeche" },
  { value: "Coahuila", label: "Coahuila" },
  { value: "Colima", label: "Colima" },
  { value: "Chiapas", label: "Chiapas" },
  { value: "Chihuahua", label: "Chihuahua" },
  { value: "Durango", label: "Durango" },
  { value: "Distrito Federal", label: "Distrito Federal" },
  { value: "Guanajuato", label: "Guanajuato" },
  { value: "Guerrero", label: "Guerrero" },
  { value: "Hidalgo", label: "Hidalgo" },
  { value: "Jalisco", label: "Jalisco" },
  { value: "México", label: "México" },
  { value: "Michoacán", label: "Michoacán" },
  { value: "Morelos", label: "Morelos" },
  { value: "Nayarit", label: "Nayarit" },
  { value: "Nuevo León", label: "Nuevo León" },
  { value: "Oaxaca", label: "Oaxaca" },
  { value: "Puebla", label: "Puebla" },
  { value: "Querétaro", label: "Querétaro" },
  { value: "Quintana Roo", label: "Quintana Roo" },
  { value: "San Luis Potosí", label: "San Luis Potosí" },
  { value: "Sinaloa", label: "Sinaloa" },
  { value: "Sonora", label: "Sonora" },
  { value: "Tabasco", label: "Tabasco" },
  { value: "Tamaulipas", label: "Tamaulipas" },
  { value: "Tlaxcala", label: "Tlaxcala" },
  { value: "Veracruz", label: "Veracruz" },
  { value: "Yucatán", label: "Yucatán" },
  { value: "y Zacatecas", label: "y Zacatecas" }
];

const SelectorsContainer = props => {
  const history = useHistory();
  const dispatch = useDispatch();
  const data = useSelector(state => state.data);

  const gettingData = useSelector(state => state.loading);
  console.log("getting data--->", gettingData);

  const [selects, setSelects] = useState({
    inmueble: null,
    operation: null,
    square: null,
    currency: null,
    city: null,
    city_area: null,
    region: null,
    price: ""
  });

  const [filterError, setFilterError] = useState(0);
  const [additionalHeight, setAdditionalHeight] = useState(props.expand);

  const expand = props.expand;

  useEffect(() => {
    if (!expand) {
      setAdditionalHeight(false);
    }
  }, [expand]);

  const onSetSelects = (value, name) => {
    setFilterError(0);
    setSelects({ ...selects, [name.name]: value });
  };

  const onChangeValue = event => {
    setSelects({ ...selects, price: event.target.value });
  };

  const filterByKey = (array, arrayKey, searchKey) => {
    return array.filter(item => item[arrayKey][0] === searchKey);
  };

  const filterByOperation = (array, arrayKey, searchKey) => {
    return array.filter(item => item[arrayKey][0].$.type === searchKey);
  };

  const filterByRegion = (array, arrayKey, searchKey) => {
    return array.filter(item => item.location[0].region[0] === searchKey);
  };

  const handleSearch = () => {
    if (gettingData) {
      alert("Please wait a second for getting all data...");
    } else if (!selects.inmueble) {
      setFilterError(1);
    } else if (!selects.operation) {
      setFilterError(2);
    } else {
      let temp;
      console.log("data-->", data);
      if (selects.inmueble)
        temp = filterByKey(data, "property_type", selects.inmueble.value);
      if (selects.operation)
        temp = filterByOperation(temp, "operation", selects.operation.value);
      if (selects.region)
        temp = filterByRegion(temp, "location", selects.region.value);
      console.log("final results->", temp);
      dispatch({ type: "searchedData", searchedData: temp });

      history.push("/property");
    }
  };

  return (
    <div className={expand ? "selectorsContainer" : "selectorsContainer-small"}>
      <div
        onClick={() => setAdditionalHeight(true)}
        className={
          expand && additionalHeight
            ? "additional-height selectsContent col-md-11"
            : "selectsContent col-md-11"
        }
      >
        <Select
          value={selects.inmueble}
          name="inmueble"
          placeholder="Inmueble"
          className={
            "col-6 col-md-3 mb-3 " +
            (filterError === 1 && "border border-danger rounded")
          }
          options={inmueble}
          onChange={onSetSelects}
        />
        <Select
          value={selects.operation}
          name="operation"
          placeholder="Operación"
          className={
            "col-6 col-md-3 mb-3 " +
            (filterError === 2 && "border border-danger rounded")
          }
          options={operation}
          onChange={onSetSelects}
        />
        <Select
          value={selects.square}
          name="square"
          placeholder="Superficie ㎡"
          className="col-6 col-md-3  mb-3"
          options={square}
          onChange={onSetSelects}
        />
        <Select
          value={selects.currency}
          name="currency"
          placeholder="MXN"
          className="col-6 col-md-3  mb-3"
          options={currency}
          onChange={onSetSelects}
        />
        <Select
          value={selects.region}
          name="region"
          placeholder="Estado"
          className="col-6 col-md-3  mb-3"
          options={region}
          onChange={onSetSelects}
        />
        <Select
          value={selects.city}
          name="city"
          placeholder="Ciudad"
          className="col-6 col-md-3 mb-3"
          options={city}
          onChange={onSetSelects}
        />
        <Select
          value={selects.city_area}
          name="city_area"
          placeholder="Zona/Colonia"
          className="col-6 col-md-3  mb-3"
          options={city_area}
          onChange={onSetSelects}
        />

        <div className="col-6 col-md-3  mb-3 inputContainer">
          <input
            placeholder="Precio máximo"
            name="price"
            value={selects.price}
            onChange={onChangeValue}
          />
        </div>
      </div>
      <div className="searchBtn col-md-1" onClick={handleSearch}>
        {gettingData ? (
          <div
            className="spinner-border text-light spinner-border-sm"
            role="status"
          >
            <span className="sr-only">Loading...</span>
          </div>
        ) : (
          <img src={SEARCH_BTN} alt="" />
        )}
      </div>
    </div>
  );
};

export default SelectorsContainer;
