import React, { useState, useEffect } from "react";
import "./App.scss";
import "bootstrap/dist/css/bootstrap.min.css";
import SideBar from "./pages/SideBar";
import Routes from "./routes";
import Header from "./pages/Header";
import { parseString } from "xml2js";
import { useDispatch } from "react-redux";

const GetStart = props => {
  // const [click, setClick] = useState(false);
  const handleClickBack = () => {
    // setClick(true);
    props.onClick();
  };

  return <div className="originalBg" onClick={handleClickBack}></div>;
};

const App = () => {
  const original = localStorage.getItem("original");
  const dispatch = useDispatch();

  const [start, setStart] = useState(false);
  useEffect(() => {
    // localStorage.setItem("original", false);
    dispatch({ type: "gettingData", loading: true });
    fetch("http://localhost:3001/api")
      .then(data => {
        console.log(data);
        parseString(data, function (err, result) {
          let temp = [];
          console.log(result);
          result.easybroker.agencies[0].agency.forEach(item => {
            item.properties[0].property.forEach(res => {
              temp.push(res);
            });
          });

          dispatch({ type: "saveData", data: temp });
          dispatch({ type: "gettingData", loading: false });
        });
      })
      .catch(err => console.log(err));
  }, [dispatch]);

  const getStart = () => {
    localStorage.setItem("original", true);
    setStart(true);
  };

  if (original === "true" || start) {
    return (
      <div className="main">
        <div className="bg" />
        <Header />
        <SideBar />
        <Routes />
      </div>
    );
  } else {
    return <GetStart onClick={getStart} />;
  }
};

export default App;
