import React, { useState } from "react";
import ImageCarousel from "../components/ImageCarousel";
import MapContainer from "../components/MapContainer";
import Properties from "./Properties";
import Currency from "../assets/svgs/dollar.svg";
import Square from "../assets/svgs/square.svg";
import Car from "../assets/svgs/car.svg";
import Bed from "../assets/svgs/beds.svg";

const Property = props => {
  console.log("props->", props);
  const [tab, setTab] = useState(1);

  const item = props.location.state;
  const location = item.location[0];

  const handleTab = index => {
    setTab(index);
  };

  return (
    <div className=" mt-5">
      <div className="container mx-auto row mb-3">
        <div className="col-12 col-md-6 col-lg-6">
          <ImageCarousel images={item.images[0].image} />
        </div>
        <div className="col-12 col-md-6 col-lg-6">
          <div className="clubTab">
            <div className="btnContainer">
              <button
                style={{
                  backgroundColor: tab === 1 ? "#233468" : "unset",
                  color: tab === 1 && "white"
                }}
                onClick={() => handleTab(1)}
              >
                DEPARTAMENTO
              </button>
              <button
                style={{
                  backgroundColor: tab === 2 ? "#233468" : "unset",
                  color: tab === 2 && "white"
                }}
                onClick={() => handleTab(2)}
              >
                OTROS
              </button>
            </div>
          </div>
          <div className="club">
            {tab === 1 && (
              <div className="club-content my-3">
                <h5>CULB RESIDENCIAL BOSQUES</h5>
                <div className="d-flex">
                  <div className="col-6 col-md-6">
                    <p>{item.property_type[0]}</p>
                    <p>
                      {location.address} {location.city} {location.region}
                    </p>
                    <p>
                      <img src={Currency} alt="" className="mr-3" />
                      2,120,000 USD
                    </p>
                    <p>
                      <img src={Square} alt="" className="mr-2" />{" "}
                      {item.construction_size[0]} ㎡
                    </p>
                  </div>
                  <div className="col-6 col-md-6">
                    {item.parking_spaces && (
                      <p>
                        <img src={Car} alt="" className="mr-2" />
                        Estacionamientos: {item.parking_spaces[0]}
                      </p>
                    )}
                    {item.bedrooms && (
                      <p>
                        <img src={Bed} alt="" className="mr-2" />
                        Recamaras: {item.bedrooms[0]}
                      </p>
                    )}
                  </div>
                </div>
              </div>
            )}
            {tab === 2 && <div>Property 2</div>}
          </div>
          <div>
            <div className="d-flex align-items-center justify-content-evenly mb-2">
              <button className="social-btn mr-3">SOLICITA INFORMACIÓN </button>
              <button className="social-btn">VER MAPA</button>
            </div>
            <h4>Detalles</h4>
            <h6>{item.description}</h6>
          </div>
        </div>
      </div>

      <MapContainer
        lat={parseInt(item.geo_location[0].latitude[0])}
        lng={parseInt(item.geo_location[0].longitude[0])}
        isMarkerShown={true}
      />
      <div className="mt-3">
        <Properties />
      </div>
    </div>
  );
};

export default Property;
